/* ================ NAVBAR ================= */
.header__background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 110px;
    background: linear-gradient(to bottom, #eceff1ff 70%, #eceff100 100%);
    z-index: 99;
}

.header__wrapper {
    background: transparent;
    height: 110px;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    padding: 0 50px;
    display: flex;
    align-items: center;
}

.header__content {
    max-width: var(--MaxWidth);
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* color: ${({color}) => color}; */
}

.header__logo {
    flex-basis: 20%;
    background-color: transparent;
    display: flex;
    align-items: center;
    height: 50px;
    width: auto;
}
.header__logo a {
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
    text-decoration: none;
    /* color: ${({color}) => color}; */
}

.header__logo img {
    height: 60%;
    margin-right: 12px;
}

.header__logo .brandName {
    font-weight: 100;
    letter-spacing: 1px;
    /* color: ${({color}) => color}; */
    }


.header__navlist {
    flex-basis: 80%;
    background: transparent;
    height: 70%;
    width: 70px;
    font-size: 2.5rem;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: flex-end;
}
.header__navlist .navbar {
    display: block;

}

.header__navlist .login {
    margin-left: 30px;
}

.header__navlist .toggler {
    display: none;
    margin-left: 30px;
}

.header__navlist .quoteBtn {
    outline: none;
    width: 120px;
    height: 80%;
    background-color: var(--Lime);
    border: none;
    border-radius: var(--Border);
    font-size: 1rem;
    padding: 10px;
    margin-bottom: 5px;

}

.header__navlist .quoteBtn * {
    color: var(--DarkNavy);

}

.header__navlist ul {
    list-style: none;
    display: flex;
    font-size: 1rem;
}

.header__navlist li {
    text-decoration: none;
    margin-right: 20px;
    display: flex;
    flex-direction: column;
}

.header__navlist li a {
    text-decoration: none;
    /* color: ${({color}) => color}; */
}

.header__navlist .underline {
    margin-top: 4px;
    height: 3px;
    width: 100%;
    /* background-color: ${({barColor}) => barColor}; */
    transform: scaleX(0);
    transition: all 330ms ease-in;
}

.header__navlist li:hover .underline {
    transform: scaleX(1);
}

@media all and (max-width: 1020px) {
    .header__navlist .navbar {
        display: none;
    }

    .header__navlist .toggler {
        display: block;
    }
}








/* ====== */






/* ========= HEADER HIDDEN TOGGLED =========== */
.header__hidden {
    position: fixed;
    top: 95px;
    right: 0px;
    opacity: .8;
    background-color: var(--Smoke);
    margin: 0 10px 0 0;
    font-size: 1.2rem;
    font-weight: 100;
    z-index: 50;
    width: 30%;
    text-align: right;
    padding-right: 50px;
}

.header__hidden li {
    writing-mode: horizontal-tb;
    margin: 20px auto;
    list-style: none;
}

.header__hidden li a {
    text-decoration: none;
    color: var(--DarkNavy);
}

@media all and (max-width: 768px) {
    .header__hidden {
        width: 100%;
        opacity: .6;
    }
}

/* ========== LOGIN FORM =========== */
.login__wrapper {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0,0,0, .7);
    z-index: 101;
}
.login__box {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login__content {
    margin: auto;
    min-width: 400px;
    min-height: 300px;
    height: 45%;
    width: 30%;
    background-color: var(--White);
    color: var(--DarkNavy);
    opacity: .9;
    font-family: 'Lato', sans-serif;
    font-size: var(--FontMed);
    font-weight: 400;
    padding: 20px 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.login__title {
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: var(--FontMed);
    font-weight: 200;
    line-height: 30px;
    height: 50px;
    border-top: rgba(0,0,200, .5);
    margin-bottom: 10px;
}

.login__close {
    cursor: pointer;
}

.login__bar {
    height: 1px;
    width: 80%;
    background-color: blue;
    opacity: .5;

}

.login__formBody {
    margin: 70px auto 30px auto;
    width: 100%;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 50px;
    font-size: 1.4rem;
}

