.login__formBody .formInput {
    width: 100%;
    display: flex;
    align-items: center;
    margin: 10px auto;
}

.login__formBody .label {
    flex-basis: 40%;
}

.login__formBody input {
    flex-basis: 60%;
    outline: none;
    height: 30px;
    margin-left: 20px;
    border: 0;
    border-bottom: 1px solid var(--DarkNavy);
    background-color: var(--Smoke);
}

.login__formBody .formCheck {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px auto;
    font-size: 1.1rem;
}

.login__formBody .formCheck input {
    flex-basis: 20%;
}

.login__formBody .checkbox {
    height: 15px !important;
    cursor: pointer;
}

.login__formBody button {
    outline: none;
    width: 120px;
    height: 70%;
    background-color: var(--Blue);
    border: none;
    border-radius: var(--Border);
    font-size: 1rem;
    padding: 10px;
    color: var(--White);
    cursor: pointer;
}
