.quote__frame {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    display: flex;
    /* background: url('../../images/contact_bg.jpg') center top/cover no-repeat; */
    justify-content: center;
    align-items: center;
    z-index: 10;
}

.quote__frame video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.quote__wrapper {

}

.quote__container {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0, .6);
}

.quote__content {
    margin: auto;
    background-color: var(--White);
    padding-top: 120px 10px 10px 10px;
    color: var(--DarkNavy);
    min-width: 400px;
    min-height: 300px;
    height: 80%;
    width: 100%;
    font-family: 'Lato', sans-serif;
    font-size: var(--FontMed);
    font-weight: 400;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.quote__header {
    max-width: var(--MaxWidth);
    width: 100%;
    margin: 30px auto 10px auto;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90px;       
    font-size: var(--FontBig);
    font-weight: 500;
    color: var(--DarkNavy);
}

.quote__bar {
    width: 70%;
    height: 6px;
    margin: 0 auto 20px auto;
    background-color: var(--Blue);
    animation: bar 3s ease-in-out infinite;
}

.quote__form {
    max-width: var(--MaxWidth);
    width: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
}

.quote__form .agent {
    flex-basis: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.agent h5 {
    font-size: .9rem;
    font-weight: 200;
    color: var(--DarkNavy);
}

.agent .staff {
    width: 320px;
    margin-bottom: 20px;
    border: 1px solid var(--Blue);
    border-radius: var(--Border);
}

.quote__formBody {
    flex-basis: 60%;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 50px;
    font-size: 1.4rem;
    margin: 0 auto;
}

.quote__formInput {
    width: 100%;
    display: flex;
    align-items: center;
    margin: 10px auto;
}

.quote__formInput input {
    width: 100%;
    outline: none;
    height: 40px;
    border: 0;
    border-bottom: 1px solid var(--DarkNavy);
    background-color: var(--Smoke);
    font-size: 1rem;
    padding: 20px;
}

.quote__formInput .textarea {
    width: 100%;
    border: 0;
    padding: 20px;
    border-bottom: 1px solid var(--DarkNavy);
    background-color: var(--Smoke);
    font-size: 1rem;
}

.quote__buttons {
    display: flex;
}

.quote__buttons button {
    outline: none;
    width: 200px;
    height: 50px;
    margin-right: 10px;
    background-color: var(--Blue);
    border: none;
    border-radius: var(--Border);
    font-size: 1rem;
    padding: 10px;
    color: var(--White);
    margin-top: 20px;
}


@media all and (max-width: 1020px) {

    .quote__header {
        font-size: var(--FontMed);
        text-align: center;
    }
    .agent {
        display: none;
    }
}

@keyframes bar {
    0% {
        transform: scaleX(.3);
    }
    50% {
        transform: scaleX(1);
    }
    100% {
        transform: scaleX(.3);
    }
}
