.about__wrapper {
    min-height: 100vh;
    height: auto;
    width: 100vw;
    background-color: var(--Smoke);
    padding-top: 80px;
}

.about__content {
    height: 100%;
    width: 100%;
    padding: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.about__title {
    max-width: var(--MaxWidth);
    margin: 50px auto;
    color: var(--DarkNavy);
    text-align: center;
    font-family: 'Roboto', sans-serif;
}

.aboutservices {
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin: 50px auto 70px auto;
}

.aboutservice {
    flex-basis: 30%;
    padding: 0;
    margin: 0;
    height: 100%;
    width: 100%;
}

.aboutservice h3 {
    height: 100%;
    width: 100%;
    transition: all 380ms ease;
    opacity: .8;
    flex-basis: 30%;
    min-height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--Blue);
    color: var(--White);
    padding: 0;
    font-size: 1.4rem;
}

.aboutservice h3:hover {
    transform: scale(1.05);
    box-shadow: 4px 4px 10px 2px rgba(0,0,0,.4);
    opacity: 1;
}

.about__bigtitle {
    font-size: 4rem;
    font-family: 'Noto Sans', sans-serif;
    color: var(--DarkNavy);
}

.about__title h2 {
    font-size: 2rem;
    margin: 20px auto;
    line-height: 50px;
    font-weight: 100;
}


.about__title  h5 {
    font-size: 1.2rem;
    margin-top: 5px;
    font-weight: 100;
    color: var(--DarkNavy);

}

@media all and (max-width: 1020px) {
    .about__title > h1 {
        font-size: 2.4rem;
    }

    .about__title > h2 {
        font-size: 1.4rem;
        line-height: 32px;
    }

    .about__title > h3 {
        font-size: 1.3rem;
    }

    .aboutservices {
        flex-direction: column;
    }

    .aboutservice {
        flex-basis: 100%;
        margin: 10px auto;
        width: 60%
    }

    .aboutservice > h3 {
        font-size: 1.3rem;
    }

    .about__cards {
        width: 90vw;
    }
}

.about__cards {
    max-width: var(--MaxWidth);
    margin: 10px auto 50px auto;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}
    
.about__card {
    height: 200px;
    width: 200px;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
}

.about__card > img {
    width: 90%;
    height: auto;
    border: 1px solid rgba(0,0,0,.3);
    border-radius: 3px;
}