
.home__background {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    z-index: -100;
}

.home__background video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.home__wrapper {
    height: 100vh;
    width: 100vw;
    border: 10px solid var(--Smoke);
    background: linear-gradient(to right, #000023 35%, rgba(255,255,255,.3) 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--DarkNavy);
}

.home__content {
    width: var(--MaxWidth);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    padding: 30px;
}

.home__text {
    width: 100%;
    height: 60%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 auto;
    font-family: 'Ubuntu', sans-serif;
}
    
* {
   text-decoration: none;
}

.home__text h1 {
    color: var(--White);
    font-size: 4rem;
    font-weight: 400;
    margin: 40px 0;
    font-family: 'Noto Sans', sans-serif;
    letter-spacing: 1px;
}

.home__text h3 {
    font-size: 1.8rem;
    font-weight: 100;
    color: var(--White);    
    letter-spacing: 1px;
    text-transform: uppercase;
}

.home__title1 {
    opacity: 0;
    animation: textFadeIn 1s .1s ease-in forwards;
}
.home__title2 {
    opacity: 0;
    animation: textFadeIn 1s .4s ease-in forwards;
}

.home__slide1 {
    transform: translateX(-500px);
    animation: SlideFromLeft 1s .1s ease-in forwards;
}
.home__slide2 {
    transform: translateX(500px);
    animation: SlideFromRight 1s .4s ease-in forwards;
}

@keyframes textFadeIn {
    0% {
        opacity: 0;
    }
    40% {
        opacity: 0;
    }
    
    100% {
        opacity: 1;
    }
}

@keyframes SlideFromLeft {
    0% {
        transform: translateX(-500px);
    }
    100% {
        transform: translateX(0px);
    }
}
@keyframes SlideFromRight{
    0% {
        transform: translateX(500px);
    }
    100% {
        transform: translateX(0px);
    }
}

.home__contactBtn {
    outline: none;
    height: 50px;
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    text-decoration: none;
    background-color: var(--Lime);
    color: var(--DarkNavy);
    padding: 30px;
    font-size: 1.3rem;
    letter-spacing: 1px;
    margin-top: 20px;
    opacity: 0;
    animation: textFadeIn 1s ease-in 1s forwards;
    border: none;
}

@media all and (max-width: 1480px) {
    .home__content {
        width: 80%;
    }

    .home__text h1 {
        font-size: 3rem;
    }

    .home__text h3 {
        font-size: 1.7rem;
    }
}

@media all and (max-width: 1020px) {



    .home__text {
        width: 90%;
        margin: 0 auto;
    }

    .home__text h1 {
        font-size: 2.3rem;
        margin: 30px 0;
    }

    .home__text h3 {
        font-size: 1.1rem;
    }


}
