.contact__background {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    z-index: -100;
    /* background: url('../../images/contact_bg.jpg') center top/cover no-repeat; */
}

.contact__background  video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    
}

.contact__wrapper {
    min-height: 100vh;
    width: 100vw;
    height: auto;
    position: fixed;
    top: 0;
    left: 0;
    color: var(--White);
    border: 10px solid var(--Smoke);
    background: linear-gradient(to right, #00002380 0%, #000023d0 100%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contact__content {
    max-width: var(--MaxWidth);
    width: 80%;
    margin: 30px auto 0 auto;
    padding: 40px;
    display: flex;
    flex-direction: column;
    align-items: cener;
    background-color: rgba(255,255,255,.3);
    border-radius: var(--Border);
}

.contact__title {
    width: 100%;
    margin: 0 auto 50px auto;
    text-align: center;
}

.contact__title h1 {
    font-family: "Noto Sans", sans-serif;
    font-size: 3rem;
    font-weight: 300;
    margin-bottom: 30px;
}

.contact__title h3 {
    font-family: 'Ubuntu', sans-serif;
    font-size: 1.7rem;
    line-height: 40px;
    font-weight: 100;
}


/* ================= contact form ================== */

.contact__form {
    width: 100%;
    height: 100%;
    display: flex;
}
.contact__form .left {
    flex-basis: 40%;
    padding-right: 100px;
    text-align: right;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.contact__form .icon {
    margin-right: 12px;
    font-size: 1.2rem;
}

.contact__form p {
    background-color: var(--Lime);
    font-weight: 100;
    align-self: flex-end;
    margin: 10px 0;
    padding: 8px;
    width: 42%;
    min-width: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contact__form a {
    text-decoration: none;
    color: var(--DarkNavy);
    font-size: 1.2rem;
}
    

.contact__form h2 {
    font-weight: 100;
    font-size: 2rem;
    margin-bottom: 20px;
    letter-spacing: 1px;
    color: var(--Blue);
}

.contact__form h3 {
    font-weight: 100;
    font-size: 1.3rem;
    line-height: 30px;
    margin-bottom: 10px;
}

.contact__form .right {
    flex-basis: 60%;
    width: 100%;
}

.contact__form .form-group {
    width: 80%;
}

.contact__form .form__info, .contact__form .form__feedback, .contact__form .form__option  {
    width: 100%;
    border: none;
    outline: none;
    background: var(--Smoke);
    color: #00002370;
    opacity: .8;
    margin-bottom: 10px;
    font-size: 1rem;
    padding: 0 10px;
}

.contact__form .form__info, .contact__form .form__option, .contact__form .form__checkbox {
    height: 35px;
}

.contact__form .form__check {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin: 10px 0 20px 0;
    height: 100%;
}

.contact__form .form__check__label {
    flex-basis: 50%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 100%;        
}

.contact__form .check__label {
    font-size: 1rem;
    text-align: left;
    flex-basis: 70%;
    align-items: center;
}

.contact__form .form__checkbox {
    flex-basis: 30%;
    background: var(--Smoke);
}

.contact__form .form__option {
    flex-basis: 50%;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0;
}

.contact__form .form__feedback {
    padding: 10px;
    font-size: 1rem;
}

.contact__form button {
    width: 100%;
    min-height: 50px;
    border: none;
    outline: none;
    font-size: 1.2rem;
    padding: 12px;
    background-color: var(--Blue);
    color: var(--White);
    margin-bottom: 10px;
}

.text-danger {
    color: var(--Pink);
}

/* ================= Media Query ================= */
@media all and (max-width: 1020px) {

    .contact__title h1 {
        font-size: 1.7rem;
        font-weight: 600;
        margin: 0;
    }

    .contact__title h3 {
        display: none;
    }

    .contact__form {    
    flex-direction: column;
    }

    .contact__form .left {
        text-align: left;
        padding: 0;
        align-items: flex-start;
        flex-direction: row;
        width: 100%;
    }

    .contact__form .address, .contact__form .contacts {
        flex-basis: 50%;
    }
    .contact__form h2 {
        font-size: 1.7rem;
        margin: 5px;
    }
    .contact__form h3 {
        font-size: 1.1rem;
        line-height: 1.5rem;
    }

    .contact__form .contacts {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }

    .contact__form  p {
        height: 35px;

    }

    .contact__form a {
        font-size: 1rem;
    }

    .contact__form .right {
        margin-top: 30px;
    }
    .contact__form .form-group {
        width: 100%;
    }
}

@media all and (max-width: 768px) {

    .contact__title h1 {
        font-size: 1.4rem;
    }

    .contact__form .address {
        display: none;
    }

    .contact__form .contacts {
        margin: 0 auto;
    }

    .contact__form button {
        font-size: 1rem;
    }
}

