.services__wrapper {
    min-height: 100vh;
    width: 100vw;
    height:  auto;
    background-color: var(--Smoke);
    padding-top: 110px;
}

.services__content {
    max-width: var(--MaxWidth);
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 30px;
}

.services__content button {
    text-decoration: none;
    color: var(--Lime);
    font-size: var(--FontMed);
    background-color: transparent;
    border: none;
    outline: none;
}

.services__quote__button {
    width: 40%;
    outline: none;
    border: none;
    border-radius: var(--Border);
    margin-top: 30px;
    height: 55px;
    background-color: var(--Blue);
    display: flex;
    justify-content: center;
    align-items: center;
}

.services__text {
    width: 100%;
    text-align: center;
    margin-bottom: 30px;
}
.services__text h1 {
    font-size: 4rem;
    font-family: 'Noto Sans', sans-serif;
    color: var(--DarkNavy);
}

.services__text h2 {
    font-size: 2rem;
    color: var(--DarkNavy);
    margin: 20px auto;
    line-height: 40px;
    font-weight: 100;
}

.services__cards {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.services__card__front, .services__card__back {
    flex-basis: 25%;
    height: 400px;
    width: 350px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 35px;
    margin-bottom: 30px;
    box-shadow: 10px 10px 20px rgba(0,0,0,.3);
    transition: all 330ms ease-in;
}

.services__card__back:hover, .services__card__front:hover {
    transform: scale(1.05);
    box-shadow: 20px 20px 30px rgba(0,0,209,.6);
}

.services__card__front {
    background-color: var(--White);
    color: var(--DarkNavy);
    justify-content: space-between;
}

.service__image {
    flex-basis: 50%;
    margin-bottom: 20px;
}

.service__image img {
    max-height: 140px;
    width: auto;
    object-fit: cover;
}

.service__details {
    margin-top: 15px;
    flex-basis: 30%;

}
.services__card__front h1 {
    font-family: 'Noto Sans', sans-serif;
    color: var(--DarkNavy);
    font-size: 1.5rem;
    text-align: center;
}

.service__details h3 {
    margin: 5px;
    color: gray;
    font-weight: 100;
    font-size: 1.2rem;
} 

.services__card__back {
    justify-content: center;
    background-color: var(--Blue);
    color: var(--White);
    line-height: 35px;
}
.services__card__back h3 {
    font-weight: 100;
    font-size: 1.5rem;
}


@media all and (max-width: 1180px) {
    .services__cards {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}

@media all and (max-width: 1020px) {
    .services__text h1 {
        font-size: 2.4rem;
    }

    .services__text h2 {
        font-size: 1.4rem;
        line-height: 32px;
    }
}