.team__wrapper {
    width: 100vw;
    height: auto;
    padding: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--Smoke);
}

.team__content {
    max-width: var(--MaxWidth);
    width: 100%;
    margin: 0 auto;
}

.team__text {
    margin-top: 50px;
    text-align: center;
    color: var(--DarkNavy);
}

.team__bigtitle {
    font-size: 4rem;
    font-family: 'Noto Sans', sans-serif;
}

.team__description {
    margin: 20px auto;
    font-weight: 100;
    font-size: 2rem;
}

.staff__grid {
    width: 100%;
    margin-top: 50px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-gap: 4rem;
}
.staff__card {
    width: 350px;
    margin: auto;
    padding: 0;
    background-color: var(--White);
    box-shadow: 10px 5px 25px rgba(0,0,0,.3);
    transition: all 320ms ease-in;
}

.staff__card:hover {
    box-shadow: 12px 12px 30px rgba(0,0,0,.2);
    transform: scale(1.04) ;
}
.staff__card img {
    height: auto;
    width: 100%
}

.staff__details {
    width: 100%;
    height: 230px;
    padding: 13px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.staff__details h2 {
    text-align: center;
    margin: 8px auto;
    font-size: 1.6rem;
}

.staff__details h4 {
    font-weight: 100;
    font-size: 1.1rem;
}

.staff__email {
    border: 1px solid var(--Orange);
    padding: 5px 7px;;
    text-decoration: none;
}

.staff__email a {
    text-decoration: none;
    color: var(--Orange);
    text-transform: uppercase;
}
